.wrapper {
  width: 100%;
  background: #ffffff;
  border-radius: 14px;
  overflow: hidden;
  margin-bottom: 20px;
}

/* COVER */
.cover {
  height: 120px;
  background: linear-gradient(135deg, #7f00ff, #9a33ff);
}

/* CONTENT */
.content {
  display: flex;
  gap: 20px;
  padding: 0 24px 20px;
  margin-top: -50px;
  align-items: center;
}

/* LEFT */
.left {
  flex-shrink: 0;
}

.avatar {
  width: 96px;
  height: 96px;
  border-radius: 50%;
  object-fit: cover;
  border: 4px solid #ffffff;
}

.avatarFallback {
  width: 96px;
  height: 96px;
  border-radius: 50%;
  background: #7f00ff;
  color: #ffffff;
  font-size: 36px;
  font-weight: 800;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 4px solid #ffffff;
}

/* RIGHT */
.right {
  flex: 1;
}

.name {
  font-size: 22px;
  font-weight: 700;
  margin-bottom: 6px;
}

.role {
  font-size: 14px;
  font-weight: 500;
  color: #666;
  margin-left: 8px;
}

/* STATS */
.stats {
  display: flex;
  gap: 20px;
  font-size: 14px;
  color: #555;
  margin-bottom: 6px;
}

.stats span {
  cursor: pointer;
  font-weight: 600;
}

.stats span:hover {
  color: #7f00ff;
}

/* RANK */
.rank {
  font-size: 13px;
  color: #888;
}
